"use client";

import AccountsTable from '../../../components/tables/AccountsTable';
import Modal from '../../../components/ui/Modal';
import AccountForm from '../../../components/forms/AccountForm';
import React, { useState, useEffect } from 'react';


// Demo tags for filter dropdown (should be fetched from API in real app)
const DEMO_TAGS = ['VIP', 'Prospect', 'Active'];




export default function AccountsPage() {
  // ...existing code...
  const [modalOpen, setModalOpen] = useState(false);
  const [search, setSearch] = useState("");
  const [typeFilter, setTypeFilter] = useState('');
  const [ownerFilter, setOwnerFilter] = useState('');
  const [dateFrom, setDateFrom] = useState("");
  const [dateTo, setDateTo] = useState("");
  const [owners, setOwners] = useState<string[]>([]);
  const [types, setTypes] = useState<string[]>([]);
  const [sortBy, setSortBy] = useState('createdAt-desc');
  const [tagFilter, setTagFilter] = useState('');

  // Fetch unique owners/types for filters (simulate API or get from /api/accounts)
  useEffect(() => {
    fetch('/api/accounts')
      .then(res => res.json())
      .then(data => {
        setOwners(Array.from(new Set(data.map((a: any) => a.ownerUserId).filter(Boolean))));
        setTypes(Array.from(new Set(data.map((a: any) => a.type).filter(Boolean))));
      });
  }, []);

  const handleCreate = (data: any) => {
    // TODO: Connect to backend API
    setModalOpen(false);
    // Optionally refresh table data here
  };

  return (
    <div style={{ padding: 24 }}>
      <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 24 }}>
        <div>
          <h1 style={{ fontSize: 28, fontWeight: 700, margin: 0 }}>Accounts</h1>
          <div style={{ color: '#888', fontSize: 15, marginTop: 2 }}>Manage all your organizations and clients</div>
        </div>
        <button onClick={() => setModalOpen(true)} style={{ background: '#0052cc', color: '#fff', border: 'none', borderRadius: 6, padding: '8px 20px', fontWeight: 600, cursor: 'pointer' }}>+ New Account</button>
      </div>
      {/* Jira-style search and filters */}
      <div style={{ display: 'flex', alignItems: 'center', gap: 16, marginBottom: 16, background: '#f4f5f7', borderRadius: 8, padding: 16, flexWrap: 'wrap' }}>
        <select value={tagFilter} onChange={e => setTagFilter(e.target.value)} style={{ padding: 10, borderRadius: 6, border: '1.5px solid #b3bac5', background: '#fff', fontSize: 15 }}>
          <option value="">All Status/Tags</option>
          {DEMO_TAGS.map(tag => <option key={tag} value={tag}>{tag}</option>)}
        </select>
        <input
          type="text"
          placeholder="Search name, email, owner..."
          value={search}
          onChange={e => setSearch(e.target.value)}
          style={{ padding: 10, borderRadius: 6, border: '1.5px solid #b3bac5', width: 220, fontSize: 15, background: '#fff' }}
        />
        <select value={typeFilter} onChange={e => setTypeFilter(e.target.value)} style={{ padding: 10, borderRadius: 6, border: '1.5px solid #b3bac5', background: '#fff', fontSize: 15 }}>
          <option value="">All Types</option>
          {types.map(type => <option key={type} value={type}>{type}</option>)}
        </select>
        <select value={ownerFilter} onChange={e => setOwnerFilter(e.target.value)} style={{ padding: 10, borderRadius: 6, border: '1.5px solid #b3bac5', background: '#fff', fontSize: 15 }}>
          <option value="">All Owners</option>
          {owners.map(owner => <option key={owner} value={owner}>{owner}</option>)}
        </select>
        <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
          <label style={{ fontSize: 14, color: '#555' }}>Created:</label>
          <input type="date" value={dateFrom} onChange={e => setDateFrom(e.target.value)} style={{ padding: 7, borderRadius: 6, border: '1.5px solid #b3bac5', background: '#fff', fontSize: 14 }} />
          <span style={{ fontSize: 14, color: '#888' }}>to</span>
          <input type="date" value={dateTo} onChange={e => setDateTo(e.target.value)} style={{ padding: 7, borderRadius: 6, border: '1.5px solid #b3bac5', background: '#fff', fontSize: 14 }} />
        </div>
        <button onClick={() => { setSearch(""); setTypeFilter(""); setOwnerFilter(""); setDateFrom(""); setDateTo(""); }} style={{ marginLeft: 8, background: '#fff', color: '#0052cc', border: '1.5px solid #b3bac5', borderRadius: 6, padding: '8px 16px', fontWeight: 600, cursor: 'pointer', fontSize: 15 }}>Clear Filters</button>
        <select value={sortBy} onChange={e => setSortBy(e.target.value)} style={{ marginLeft: 8, padding: 10, borderRadius: 6, border: '1.5px solid #b3bac5', background: '#fff', fontSize: 15 }}>
          <option value="createdAt-desc">Sort: Newest</option>
          <option value="createdAt-asc">Sort: Oldest</option>
          <option value="name-asc">Sort: Name A-Z</option>
          <option value="name-desc">Sort: Name Z-A</option>
          <option value="type-asc">Sort: Type A-Z</option>
          <option value="type-desc">Sort: Type Z-A</option>
          <option value="ownerUserId-asc">Sort: Owner A-Z</option>
          <option value="ownerUserId-desc">Sort: Owner Z-A</option>
        </select>
      </div>
      <div style={{ background: '#fff', borderRadius: 8, padding: 24 }}>
        <AccountsTable search={search} typeFilter={typeFilter} ownerFilter={ownerFilter} dateFrom={dateFrom} dateTo={dateTo} sortBy={sortBy} tagFilter={tagFilter} />
      </div>
      <Modal open={modalOpen} onClose={() => setModalOpen(false)} title="New Account">
        <AccountForm onSubmit={handleCreate} />
      </Modal>
    </div>
  );
}
